import React from 'react'

export default function PrivacyPage() {
  const lastUpdated = 'July 13, 2025'

  return (
    <main style={{ padding: 24, maxWidth: 900, margin: '0 auto' }}>
      <h1 style={{ fontSize: 28, marginBottom: 8 }}>Privacy Policy</h1>
      <p style={{ color: '#666', marginBottom: 24 }}>Last updated: {lastUpdated}</p>

      <section style={{ marginBottom: 18 }}>
        <h2 style={{ fontSize: 18, marginBottom: 8 }}>1. Introduction</h2>
        <p style={{ color: '#333' }}>
          This Privacy Policy explains how Qwesi collects, uses, and discloses information when you use our services.
        </p>
      </section>

      <section style={{ marginBottom: 18 }}>
        <h2 style={{ fontSize: 18, marginBottom: 8 }}>2. Information We Collect</h2>
        <p style={{ color: '#333' }}>
          We may collect personal information such as name, email, phone number, payment information and usage data.
        </p>
      </section>

      <section style={{ marginBottom: 18 }}>
        <h2 style={{ fontSize: 18, marginBottom: 8 }}>3. How We Use Information</h2>
        <p style={{ color: '#333' }}>
          We use information to provide, maintain, and improve our services, process transactions, and communicate with you.
        </p>
      </section>

      <section style={{ marginBottom: 18 }}>
        <h2 style={{ fontSize: 18, marginBottom: 8 }}>4. Sharing and Disclosure</h2>
        <p style={{ color: '#333' }}>
          We may share information with service providers, payment processors, and as required by law. We do not sell personal data.
        </p>
      </section>

      <section style={{ marginBottom: 18 }}>
        <h2 style={{ fontSize: 18, marginBottom: 8 }}>5. Data Retention and Security</h2>
        <p style={{ color: '#333' }}>
          We retain personal data as necessary and use industry-standard security measures to protect it, though no method is completely secure.
        </p>
      </section>

      <section style={{ marginBottom: 18 }}>
        <h2 style={{ fontSize: 18, marginBottom: 8 }}>6. Your Rights</h2>
        <p style={{ color: '#333' }}>
          You may request access, correction, or deletion of your personal data by contacting us at info@qwesi.org.
        </p>
      </section>

      <section style={{ marginBottom: 18 }}>
        <h2 style={{ fontSize: 18, marginBottom: 8 }}>7. Contact</h2>
        <p style={{ color: '#333' }}>
          For privacy questions: info@qwesi.org
        </p>
      </section>
    </main>
  )
}